<template>
  <div>
    <h1 class="title">WPM:初始化设置</h1>

    <div class="options">
      <div class="option" @click="doImportPreset">导入预置设置 (默认)</div>
      <div class="option" @click="doImportSaved">导入之前导出的设置</div>
      <div class="option" @click="doNothing">不导入任何设置，直接使用</div>
    </div>
  </div>
</template>

<script>
export default {
  name: "init-setup-or-import",

  data() {
    return {};
  },

  methods: {
    doImportSaved() {
      this.$router.push("/settings/backups");
    },

    doImportPreset() {
      this.$router.push("/setup");
    },

    doNothing() {
      this.$store.dispatch("setup/skipall", {}).then(() => {
        this.$router.push("/");
      });
    },
  },

  mounted() {},
};
</script>

